import React from "react";
import { Input, Button, List } from "antd";
import { actionInputValue, actionHandleButton } from "../store/actions"
import { connect } from 'react-redux'
import {TweenMax, TimelineLite} from "gsap/TweenMax";

class ReactReduxForm extends React.Component {
  constructor(props){
    super(props)
    this.state = {

    }
    this.btn = React.createRef()
  }
  componentWillMount(){
    console.log('componentWillMount')
  }
  componentDidMount(){
    console.log(this.btn.current)
    const Timeline = new TimelineLite()
    Timeline.to(
      this.btn.current,
      2,
      {
        x: "+=300px"
      }
    ).to(
      this.btn.current,
      2,
      {
        y: "+=300px"
      }
    )
  }
  render() {
    console.log('render')
    return (
      <div>
        <Input
          style={{ margin: "16px 0px" }}
          size="large"
          placeholder={this.props.InputValue}
          value={this.props.InputValue}
          onChange={this.props.HandleValue}
        />
        <div ref = {this.btn}><Button type="primary" onClick={this.props.HandleButton}></Button></div>
        <p>{this.props.InputValue}</p>
        <List
          header={<div></div>}
          bordered
          dataSource={this.props.data}
          renderItem={item => <List.Item>{item}</List.Item>}
        />
      </div>
    );
  }
}
const mapStateToProps = (state) => {//state
  return {
    InputValue: state.InputValue,  //storestate.InputValuethis.props.InputValue
    data: state.data
  }
}
const mapDispatchToProps = (dispatch) => {//state
  return {
    HandleValue(e){ // HandleValueprops
      dispatch(actionInputValue(e.target.value))
    },
    HandleButton(){
      dispatch(actionHandleButton())
    }
  }
}
//connectReduxFormUIReduxForm
// connectmapStateToProps, mapDispatchToPropsReduxForm
export default connect(mapStateToProps, mapDispatchToProps)(ReactReduxForm);



